.articals {
    .editor-wrapper {
        height: 500px;
        box-shadow: 0 0 5px rgba(0, 0, 0, .1);
        overflow: hidden;
    }

    .card-title {
        position: relative;
        font-size: 16px;
        font-weight: bolder;
    }

    .a-form {
        width: 100%;
        height: 33px;
        margin: 20px 0;
    }

    .a-form .ant-input {
        width: 875px;
    }

    .card-title::before {
        content: '';
        position: absolute;
        width: 3px;
        height: 24px;
        left: -24px;
        top: 1px;
        background-color: #0081ff;
    }

    .setting-form {
        widows: 100px;
        height: 200px;
        border: 1px solid #d9d9d9;
    }
}